.footer {
    background-color: $color-bg-black;
    position: relative;

    @include respondTo(mobile) {
        overflow-x: hidden;
    }

    &__wrapper {
        padding: 4rem .5rem 1.25rem;//64px 8px 20px

        @include respondTo(desktop) {
            padding: 4rem 4rem 1.625rem;//64px 64px 26px
        }

        @include respondTo(mobile) {
            padding: 2rem 1.25rem 1.5rem;//32px 20px 24px
        }
    }

    &__top {
        align-items: flex-start;
        display: flex;
        justify-content: space-between;

        @include respondTo(desktop) {
            align-items: center;
        }

        @include respondTo(mobile) {
            display: block;
        }
    }

    &__contacts {
        display: flex;
        margin: 0 -1.875rem;//0 -30px

        @include respondTo(desktop) {
            flex-direction: column;
        }

        a {
            color: $color-text-white;
            font-family: $ff-semi-bold;
            font-size: 1.75rem;//28px
            letter-spacing: -0.01em;
            line-height: 1.14;
            margin: 0 1.875rem;//0 30px
            transition: .3s color;
            white-space: nowrap;
            
            &:hover {
                color: $color-text-accent;
            }

            @include respondTo(desktop) {
                
                &:nth-of-type(2) {
                    letter-spacing: -0.01em;
                    margin-top: .25rem;//4px
                }
            }

            @include respondTo(tablet) {
                font-size: 1.625rem;//26px
            }

            @include respondTo(mobile) {
                font-size: 1.5rem;//24px
                letter-spacing: -0.015em;

                &:nth-of-type(2) {
                    margin-top: 0;
                }
            }
        }
    }

    &__others {
        align-items: center;
        display: flex;
        margin: 0 -1rem;//0 -16px

        @include respondTo(tablet) {
            margin: 0 -.75rem;//0 -12px
        }

        @include respondTo(mobile) {
            margin-top: 2.25rem;//36px
        }

        a {
            margin: 0 1rem;//0 16px

            @include respondTo(tablet) {
                margin: 0 .75rem;//0 12px
            }

            svg {

                path {
                    transition: .3s all;
                }
            }
        }
    }

    &__consult {
        
        &:hover {

            svg {
            
                path {
                    fill: $color-primary;
                }
            }
        }

        @include respondTo(tablet) {

            svg {
                height: 36px;
                width: 125.34px;
            }
        }

        @include respondTo(mobile) {

            svg {
                height: 24px;
                width: 83.56px;
            }
        }
    }

    &__estate {

        &:hover {

            svg {
            
                path {
                    fill: $color-primary;
                }
            }
        }

        @include respondTo(tablet) {

            svg {
                height: 36px;
                width: 107.6px;
            }
        }

        @include respondTo(mobile) {

            svg {
                height: 24px;
                width: 71.73px;
            }
        }
    }

    &__national {

        &:hover {

            svg {
            
                path {
                    fill: $color-primary;

                    &:nth-of-type(3) {
                        fill: $color-text;
                    }
                }
            }
        }

        @include respondTo(tablet) {

            svg {
                height: 56px;
                width: 73.5px;
            }
        }

        @include respondTo(mobile) {

            svg {
                height: 32px;
                width: 42px;
            }
        }
    }

    &__bottom {
        border-top: 1px solid $color-text-gray;
        display: flex;
        margin-top: 3rem;//48px
        padding-top: 1rem;//16px

        @include respondTo(desktop) {
            margin-top: 2.5rem;//40px
        }

        @include respondTo(mobile) {
            display: block;
            margin-top: 1.5rem;//24px
            padding-top: .75rem;//12px
        }
    }

    &__soc {
        display: flex;
        margin: 0 -.5rem;// 0 -8px

        a {
            margin: 0 .5rem;//0 8px

            svg {

                path {
                    transition: .3s fill;
                }

                @include respondTo(mobile) {
                    height: 1.5rem;//24px
                    width: 1.5rem;//24px
                } 
            }

            &:hover {

                svg {

                    path {
                        fill: $color-text-white;
                    }
                }
            }
        }
    }

    &__under {
        align-items: center;
        color: $color-text-gray;
        display: flex;
        flex-wrap: wrap;
        font-family: $ff-medium;
        font-size: .875rem;//14px
        justify-content: space-between;
        line-height: 1.29;
        padding: 0 0 0 2.25rem;//0 0 0 40px
        position: relative;
        width: 100%;

        @include respondTo(tablet) {
            font-size: .75rem;//12px
            justify-content: flex-end;
            line-height: 1.16;
            padding: 0;
        }

        @include respondTo(mobile) {
            align-items: flex-start;
            flex-direction: column;
            font-size: .625rem;//10px
            margin-top: 1.5rem;//24px
            line-height: 1.2;
        }

        a {
            color: $color-text-gray;
            transition: .3s all;

            &:hover {
                color: $color-text-white;
            }

            @include respondTo(tablet) {
                margin-right: 1.25rem;//20px
            }

            @include respondTo(mobile) {
                margin-right: 0;
                margin-top: .25rem;//4px
                order: 1;
            }
        }
    }

    &__captcha {
        display: block;
        margin-top: 1rem;
        width: 100%;

        @include respondTo(tablet) {
            padding-left: 2rem;
            text-align: end;
        }

        @include respondTo(mobile) {
            margin-top: .25rem;
            order: 2;
            padding-left: 0;
            text-align: start;
        }

        a {
            margin: 0;
        }
    }
}