.sidebar-directions{
  position: absolute;
  z-index: 1001;
  top: 0;
  left: 0;
  width: 100%;
  max-width: 856px;
  padding: 31px 10px 60px 41px;
  background-color: #fff;
  transition-duration: .8s;
  transform: translateX(-100%);
  will-change:transform;
  &.active{
    transform: translateX(0);
  }
  @include respondTo(mobile) {
    padding-left: 10px;
  }
  &__wrap-logo{
    display: flex;
  }
  &__close{
    cursor: pointer;
    margin-right: 33px;
    padding-top: 9px;
  }
  &__logo{
    cursor: pointer;
    max-width: 48px;
  }
  &__link{
    margin-top: 73px;
    display: grid;
    grid-template-columns: repeat(2,minmax(200px,368px));
    grid-column-gap: 16px;
    grid-row-gap: 30px;
    @include respondTo(small) {
      grid-template-columns: 1fr;
    }
  }
  &__link-item{
    padding-left: 30px;
    margin-bottom: 58px;
    width: 100%;
    max-width: 320px;
    display: flex;
    &:hover .sidebar-directions__link-title{
      text-shadow: 0 0 3px #c2c0c0;
    }
    @include respondTo(mobile) {
      padding-left: 15px;
    }
    @include respondTo(small) {
      margin-bottom: 10px;
    }
  }
  &__link-img{
    flex-shrink: 0;
    max-width: 58px;
    margin-right: 28px;
  }
  &__link-title{
    letter-spacing: 0.02em;
    text-transform: uppercase;
    font-size: 16px;
    line-height: 24px;
    font-family: $ff-bold;
  }
  &__link-des{
    color: #000000;
    opacity: 0.5;
    font-size: 14px;
    line-height: 18px;
  }
}
#lean_overlay {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.25);
  display: none;
}
