.card-block-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -.625rem;
  margin-right: -.625rem;
  margin-bottom: -1.25rem;

  @include respondTo(mobile) {
    margin-left: -.5rem;
    margin-right: -.5rem;
    margin-bottom: -1rem;
  }
  @include respondTo(small) {
    margin-left: -.5rem;
    margin-right: -.5rem;
    margin-bottom: -1rem;
  }

  &__col {
    margin: 0 .625rem 1.25rem;//0 10px 20px

    // @include respondTo(tablet) {
    //     margin: 0 .625rem 1.25rem;//0 10px 20px
    // }

    @include respondTo(mobile) {
      margin: 0 .5rem 1rem;//0 8px 16px
    }
  }

  .card-list__col.full-screen-banner {
    width: 100%;
  }

  &_only-one-row {

    &.card-list_4-col {

      .card-list__col:nth-of-type(n + 5) {
        display: none;
      }

      @include respondTo(desktop) {

        .card-list__col:nth-of-type(n + 4) {
          display: none;
        }
      }

      @include respondTo(tablet) {

        .card-list__col:nth-of-type(n + 3) {
          display: none;
        }
      }
    }

    &.card-list_3-col {

      .card-list__col:nth-of-type(n + 4) {
        display: none;
      }

      @include respondTo(desktop) {

        .card-list__col:nth-of-type(n + 3) {
          display: none;
        }
      }
    }
  }

  &_4-col {

    .card-list__col {
      width: calc(25% - 1.25rem);

      @include respondTo(desktop) {
        width: calc(33.3333% - 1.25rem);
      }

      @include respondTo(tablet) {
        width: calc(50% - 1.25rem);
      }

      @include respondTo(mobile) {
        width: calc(100% - 1rem);
      }
    }
  }

  &_3-col {

    .card-list__col {
      width: calc(33.3333% - 1.25rem);

      @include respondTo(desktop) {
        width: calc(50% - 1.25rem);
      }

      @include respondTo(tablet) {
        width: calc(50% - 1.25rem);
      }

      @include respondTo(mobile) {
        width: calc(100% - 1rem);
      }
    }
  }

  &_2-col {

    .card-list__col {
      width: calc(50% - 1.25rem);

      @include respondTo(desktop) {
        width: calc(50% - 1.25rem);
      }

      @include respondTo(tablet) {
        width: calc(50% - 1.25rem);
      }

      @include respondTo(mobile) {
        width: calc(100% - 1rem);
      }
    }
  }
}
.h1-title{
  position: relative;
  z-index: 5;
  font-size: 56px;
  line-height: 64px;
  color: $color-bg-black;
  font-family: $ff-bold;
  text-align: center;
  @include respondTo(small) {
    font-size: 32px;
    line-height: 36px;
  }
  @include respondTo(mobile) {
    width: 100%;
    max-width: 646px;
    margin: 0 auto;
  }

}
.field-search{
  position: relative;
  &__mane-page{
    position: relative;
    z-index: 5;
    margin-top: 56px;
    @include respondTo(mobile) {
      margin-top: 48px;
    }
    @include respondTo(small) {
      margin-top: 39px;
    }
    .search__input{
      box-shadow: 0 20px 60px rgba(107, 112, 129, 0.15);
      @include respondTo(mobile) {
        height: 64px;
        box-shadow: 0px 10px 30px rgba(202, 204, 223, 0.5);
      }
      @include respondTo(small) {
        height: 40px;
      }

    }
  }
  &__form {
    border-radius: 37px;
    margin-bottom: 0;
  }
  .search__input{
    border-radius: 37px;
  }
}
.round-cards{
  &__answer-questions{
    position: relative;
    z-index: 1;
    margin-top: 27px;
    justify-content: flex-end;
    .round-cards__item{
      margin: 0 5px .75rem;
    }
    @include respondTo(mobile) {
      justify-content: flex-start;
    }
    @include respondTo(small) {
      margin-top: 12px;
    }

  }
}
.block-services{
  &__main{
    position: relative;
    z-index: 5;
    margin-top: 49px;
    @include respondTo(small) {
      margin-top: 28px;
    }
  }
  width: 100%;
  max-width: 1095px;
  margin-left: auto;
  margin-right: auto;
  display: grid;
  grid-template-columns: minmax(200px,623px) minmax(200px,465px);
  grid-column-gap: 10px;
  @include respondTo(small) {
    grid-template-columns:1fr;
  }
  &__col{

  }
  &__item{
    width: 100%;
    max-width: 465px;
    display: flex;
    align-items: center;
    margin-bottom: 56px;
    &:hover{
      .block-services__item-title{
        text-shadow: 0 0 3px #888383;
      }
    }
    @include respondTo(small) {
      margin-bottom: 33px;
    }
  }
  &__item-img{
    display: flex;
    justify-content: center;
    width: 100%;
    max-width: 111px;
    margin-right: 25px;
    @include respondTo(mobile) {
      max-width: 75px;
      margin-right: 20px;
    }
    @include respondTo(small) {
      max-width: 47px;
    }
  }
  &__item-wrap-des{

  }
  &__item-title{
    font-size: 30px;
    line-height: 44px;
    letter-spacing: -0.015em;
    color: #1C1B28;
    font-family: $ff-bold;
    @include respondTo(tablet) {
      font-size: 20px;
      line-height: 21px;
    }
    @include respondTo(small) {
      font-size: 18px;
      line-height: 19px;
    }
  }
  &__item-des{
    font-size: 18px;
    line-height: 28px;
    color: #686D82;
    @include respondTo(tablet) {
      font-size: 16px;
      line-height: 23px;
      margin-top: 10px;
    }
    @include respondTo(mobile) {
      font-size: 13px;
      line-height: 19px;
    }

    @include respondTo(small) {
      font-size: 12px;
      line-height: 16px;
    }
  }
}
