.block-possibilities{
  padding-top: 71px;
  padding-bottom: 108px;
  @include respondTo(mobile) {
    padding-top: 44px;
    padding-bottom: 44px;
  }
  @include respondTo(small) {
    padding-top: 28px;
    padding-bottom: 28px;
  }
}
.full-screen-banner{
  width: 100%;
  position: relative;
  overflow: hidden;
  &-border-5{
    border-radius: 5px;
  }
  &__common{
    &:before{
      content: "";
      display: block;
      padding-top: 33%;
      min-height: 408px;
    }
    .card-service__title{
      position: relative;
      font-size: 40px;
      line-height: 44px;
      letter-spacing: -0.01em;
      font-family: $ff-bold;
      margin-top: 9px;
      @include respondTo(small) {
        font-size: 24px;
        line-height: 24px;
      }
    }
    @include respondTo(tablet) {
      &:before{
        padding-top: 67%;
        min-height: 525px;
      }
    }
    @include respondTo(mobile) {
      &:before{
        padding-top: 55%;
        min-height: 368px;
      }
    }
    @include respondTo(small) {
      &:before{
        padding-top: 55%;
        min-height: 320px;
        max-height: 320px;
      }
    }
  }
  &.yellow{
    background-color: #FED141;
    &:hover{
      background-color: #f1c73f;
    }
    .card-service__type-icon{
      svg{
        path{
          fill:#1C1B28;
        }
      }
    }
  }
  &.grey{
    background-color: #F1F2F4;
    .card-service__type-icon{
      svg{
        path{
          fill:#1C1B28;
        }
      }
    }
  }
  &__body{
    display: flex;
    flex-direction: column;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    padding-top: 47px;
    padding-left: 26px;
    padding-right: 10px;
    @include respondTo(small) {
      padding: 1rem;
    }
  }
  &__questions{
    display: none;
    margin-top: 1.25rem;
    &:before{
      content: "";
      display: block;
      padding-top: 33%;
    }
    .card-service__title-block{
      margin-left: auto;
      margin-right: 42px;
      @include respondTo(mobile) {
        max-width: 240px;
      }


    }
    .card-service__title{
      position: relative;
      z-index: 1;
      font-size: 40px;
      line-height: 44px;
      letter-spacing: -0.01em;
      font-family: $ff-bold;
      @include respondTo(mobile) {
        font-size: 26px;
        line-height: 26px;
      }


    }
    .card-service__learn-more{
      font-size: 12px;
      line-height: 16px;
      font-family: $ff-bold;
      margin-top: 32px;
      color: #1C1B28;
      text-transform: uppercase;

    }


    @include respondTo(mobile) {
      display: block;
      &:before {
        padding-top: 32%;
      }
    }

  }
}
.card-service__main-page-title{
  color: #1C1B28;
  width: 100%;
  max-width: 395px;
  @include respondTo(mobile) {
    max-width: 100%;
  }
}
.card__circle-bunner{
  position: absolute;
  width: 100%;
  border-radius: 50%;
  &.circle-yellow{
    background: linear-gradient(178.86deg, rgba(255, 255, 255, 0) 4.33%, rgba(255, 255, 255, 0.7) 99.02%);
    transform: rotate(-90deg);

  }
  &.circke-blur{
    background: linear-gradient(136.66deg, #8D98F5 8.29%, rgba(196, 196, 196, 0) 87.71%);
  }
  &:before{
    content: "";
    display: block;
    padding-top: 100%;
  }
  &-main-page{
    top: 22px;
    right: 82px;
    max-width: 363px;
    @include respondTo(mobile) {
      top: 50%;
      right: 50%;
      transform: rotate(-90deg) translate(50%,50%);
    }
    @include respondTo(small) {
      max-width: 240px;
    }
  }
  &-questions{
    top: 22px;
    left: 82px;
    max-width: 363px;
    @include respondTo(mobile) {
      top: 8px;
      left: 33px;
      transform: rotate(-90deg) translate(50%,50%);
      width: 163px;
      height: 163px;

    }

    &.circle-yellow{


    }
  }

}
.card__img{
  position: absolute;
  bottom: 0;


  picture{
    display: flex;
  }
}
.card__img-width-613{
  max-width: 613px;
  right: 27px;
}
.card__img-width-410{
  max-width: 410px;
  right: 190px;
  @include respondTo(mobile) {
    right: 50%;
    transform: translateX(50%);
  }
}
.card__questions{
  max-width: 613px;
  left: 27px;
  @include respondTo(mobile) {
    max-width: 242px;
    left: 0;
  }

}
