.media-menu-btn {
  opacity: 0;
  position: absolute;
  right: 0;
  //right: calc(50% + 39.375rem);
  transition: opacity .3s,visibility .3s;
  visibility: hidden;
  top: 27px;
  z-index: 11;
  @include respondTo(desktop) {
    visibility: visible;
    opacity: 1;
  }
  @include respondTo(mobile) {
    top: 36px;
  }
  @include respondTo(small) {
    top: 26px;
  }
}
.header-buttons-main{
  .header-buttons__connection{
    @include respondTo(desktop) {
      right: 66px;
      bottom: 21px;
    }
    @include respondTo(mobile) {
      right: 40px;
      bottom: 28px;
    }
    @include respondTo(small) {
      right: 40px;
      bottom: 14px;
    }



  }
}
@media screen and (max-width: 670px) {
  .full-screen-banner__questions{
    .card-service__title-block {
      margin-left: 0;
      margin-right: 0;
      max-width: 225px;
    }
    .card-service__title {
      font-size: 24px;
      line-height: 24px;
    }
    .card-service__learn-more{
      font-size: 10px;
      line-height: 12px;
    }
    .full-screen-banner__body {
      padding: 1rem;
    }
  }

  .full-screen-banner__questions{
      min-height: 320px;
      max-height: 320px;
      &:before {
        padding-top: 64%;
      }
  }
  .card__circle-bunner-questions {
    width: 240px;
    height: 240px;
    left: 50%;
    top: 50%;
  }
  .card__circle-bunner-questions.circle-yellow {
    transform: rotate(-90deg) translate(50%, -50%);
  }
  .card__questions {
    max-width: 100%;
  }
}
