.check-status {
    background: #FFFFFF;
    border-radius: 20px;
    padding: 64px 104px;
    max-width: 1228px;
    margin: auto;

    @include respondTo(desktop) {
        margin: 0 64px;
        padding: 64px;
    }

    @include respondTo(mobile) {
        margin: 0;
        padding: 48px 20px;
    }

    &__container {
        display: flex;
        justify-content: space-between;

        @include respondTo(desktop) {
            display: block;
        }
    }

    &__form-wrapper {
        flex: 0 0 525px;

        & .check-status__hint {
            margin-bottom: 32px;

            @include respondTo(mobile) {
                margin-bottom: 24px;
            }
        }
    }

    &__card-wrapper {
        flex: 0 0 292px;

        @include respondTo(desktop) {
            max-width: 310px;
        }

        & .check-status__hint {
            margin-bottom: 12px;
        }
    }

    &__steps {
        width: 100%;
        margin-bottom: 4px;
        font-weight: 600;
        font-size: 28px;
        line-height: 1.14;
        letter-spacing: -0.01em;
        color: #CACCDF;

        @include respondTo(desktop) {
            font-size: 26px;
            line-height: 1.08;
        }

        @include respondTo(mobile) {
            font-size: 24px;
        }
    }

    &__line {
        width: 100%;
        height: 1px;
        background: linear-gradient(
            to right,
            #67A511 0%,
            #67A511 30%,
            #CACCDF 30%
        );
        margin-bottom: 32px;

        @include respondTo(mobile) {
            margin-bottom: 24px;
        }

        &.second-step {
            background: linear-gradient(
                to right,
                #67A511 0%,
                #67A511 60%,
                #CACCDF 60%
            ); 
        }

        &.third-step {
            background: #67A511; 
        }
    }

    &__title {
        font-weight: 600;
        font-size: 28px;
        line-height: 1.14;
        letter-spacing: -0.01em;
        color: #1C1B28;
        margin-bottom: 20px;

        @include respondTo(desktop) {
            font-size: 26px;
            line-height: 1.08;
        }

        @include respondTo(mobile) {
            font-size: 24px;
            margin-bottom: 16px;
        }

        &.smaller {
            font-size: 22px;
            line-height: 1.18;

            @include respondTo(desktop) {
                font-size: 20px;
                line-height: 1.2;
            }

            @include respondTo(mobile) {
                font-size: 24px;
                line-height: 1.08;
            }
        }

        &.smaller-margin {
            margin-bottom: 8px;
        }
    }

    &__text {
        font-size: 18px;
        line-height: 1.4;
        color: #1C1B28;

        @include respondTo(desktop) {
            line-height: 1.3;
        }

        @include respondTo(mobile) {
            font-size: 14px;
            line-height: 1.3;
        }

        &.bigger {
            font-size: 22px;
            margin-bottom: 32px;

            @include respondTo(mobile) {
                font-size: 20px;
                line-height: 1.2;
            }
            
            &.smaller-margin {
                margin-bottom: 8px;
            }

            & .bold {
                font-weight: bold;
                color: #1C1B28;
            }
        }

        &.smaller-margin {
            margin-bottom: 16px;
        }

        &.bigger-margin {
            margin-bottom: 20px;
        }

        &.grey {
            color: #686D82;
        }
    }

    &__hint {
        font-size: 14px;
        line-height: 1.4;
        color: #1C1B28;

        @include respondTo(desktop) {
            line-height: 1.3;
        }

        &.error {
            color: #FF0000;
            margin-top: -32px;

            @include respondTo(mobile) {
                margin-top: -24px;
            }
        }
    }

    &__link {
        font-size: 14px;
        line-height: 1.4;
        color: #8BC540;
        cursor: pointer;

        @include respondTo(desktop) {
            line-height: 1.3;
        }
    }

    &__input {
        padding: 17px 20px;
        width: 100%;
        border: 1px solid #CACCDF;
        box-sizing: border-box;
        border-radius: 8px;
        outline: none;
        margin-bottom: 12px;
        font-size: 18px;
        line-height: 1.44;

        @include respondTo(desktop) {
            padding: 12px 20px;
            font-size: 16px;
            line-height: 1.5;
        }

        @include respondTo(mobile) {
            padding: 10px 12px 10px 16px;
            line-height: 1.25;
            margin-bottom: 8px;
        }

        &:hover, &:focus {
            outline: none;
        }

        &.pin {
            max-width: 50px;
            font-size: 28px;
            line-height: 1.14;
            font-weight: 600;
            padding: 16px 16px 12px;
            margin-right: 12px;

            @include respondTo(mobile) {
                padding: 10px 11px 10px 16px;
                max-width: 41px;
                max-height: 46px;
                font-size: 24px;
                line-height: 1.08;
            }
        }

        &.error {
            border: 1px solid #FF0000;
        }
    }

    &__button {
        padding: 8px 20px;
        font-size: 18px;
        line-height: 1.6;
        color: #FFFFFF;
        background: #8BC540;
        border-radius: 40px;
        outline: none;
        cursor: pointer;
        box-sizing: border-box;
        margin-right: 12px;

        @include respondTo(desktop) {
            font-size: 16px;
            line-height: 1.5;
            margin-bottom: 64px;
        }

        @include respondTo(mobile) {
            line-height: 1.25;
            margin-bottom: 48px;
        }

        &.transparent {
            background: transparent;
            color: #1C1B28;
            border: 1px solid #8BC540;
        }

        &:hover {
            background: transparent;
            color: #1C1B28;
            border: 1px solid #8BC540;
        }
    }

    &__nowrap {
        white-space: nowrap;
    }
}
