.header-red{
  border-bottom: 1px solid rgba(202,204,223,.5);
  position: relative;
  background: #f0f2f4;
  background: -moz-linear-gradient(-45deg, #f0f2f4 0%, #e9f2f4 20%, #e9f2f4 20%, #ebf2f1 43%, #e7f3e5 58%, #e9f3e8 82%, #eff2f0 100%);
  background: -webkit-linear-gradient(-45deg, #f0f2f4 0%,#e9f2f4 20%,#e9f2f4 20%,#ebf2f1 43%,#e7f3e5 58%,#e9f3e8 82%,#eff2f0 100%);
  background: linear-gradient(135deg, #f0f2f4 0%,#e9f2f4 20%,#e9f2f4 20%,#ebf2f1 43%,#e7f3e5 58%,#e9f3e8 82%,#eff2f0 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f0f2f4', endColorstr='#eff2f0',GradientType=1 );
}

.header-container{
  padding-top: 46px;
  padding-bottom: 26px;
  display: grid;
  grid-template-columns: minmax(210px, 210px) minmax(260px, 645px);
  align-items: center;
  justify-content: space-between;
  .header__logo-wrapper{
    padding-top: 0;
    margin-bottom: 0;
  }
  .header__logo{
    max-width: 8.9375rem;
  }
  .header__phone{
    margin-left: auto;
    color: #8bc540;
    svg{
      circle{
        fill:#8bc540;
      }
    }
    &:hover path{
      animation: path-rotate  .2s ease-in-out;
      animation-fill-mode:backwards;
      transform-origin: center;
      animation-iteration-count:3;
    }
  }
  .header__nav{
    justify-content: space-between;
    margin-top: 1.375rem;
  }
  .header__nav-link{
    font-size: 0.75rem;
  }
  .header__nav-item:not(:first-of-type){
    margin-left: 0;
  }
  @include respondTo(desktop) {
    grid-template-columns: 1fr;
    justify-content: center;
    padding-top: 25px;
    padding-bottom: 19px;
    padding-left: 4rem;
    padding-right: 4rem;
  }
  @include respondTo(mobile) {
    padding-left: 0;
    padding-right: 0;
  }

}
.header-grid-row{
  padding: 30px 20px;
  display: grid;
  grid-template-columns: 1fr minmax(201px,1fr) 1fr;
  justify-content: space-between;

}
.header{
  &__wrap-for-logo{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo-type-1{
    position: relative;
    z-index: 10;
    width: 100%;
    max-width: 210px;
    @include respondTo(desktop) {

    }
    @include respondTo(small) {
      max-width: 117px;
    }
  }

  &__logo-img-type-1{
    max-width: 143px;
    @include respondTo(small) {
      max-width: 92px;
    }
  }

  &__col-right{
    position: relative;
    z-index: 10;
    margin-top: -34px;
    display: flex;
    flex-direction: column;
    @include respondTo(desktop) {
      display: none;
    }
  }
  &__logo-colored-square{
    position: relative;
    cursor: pointer;
    width: 24px;
    height: 24px;
    flex-shrink: 0;
    transition-duration: .8s;
    &:hover{
      transform: rotate(180deg);
    }
    &:hover .header__logo-colored-square-item:first-child{
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
    }
    &:hover .header__logo-colored-square-item:nth-child(2){
      right: 50%;
      top: 50%;
      transform: translate(50%,-50%);
    }
    &:hover .header__logo-colored-square-item:nth-child(3){
      left: 50%;
      bottom: 50%;
      transform: translate(-50%,50%);
    }
    &:hover .header__logo-colored-square-item:nth-child(3){
      left: 50%;
      bottom: 50%;
      transform: translate(-50%,50%);
    }
    &:hover .header__logo-colored-square-item:nth-child(4){
      right: 50%;
      bottom: 50%;
      transform: translate(50%,50%);
    }


    @include respondTo(small) {
      margin-right: 15px;
    }
  }
  &__logo-colored-square-item{
    position: absolute;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    transition-duration: .5s;
    will-change: transform;
    //will-change: left, top, right, bottom;
    &:first-child{
      background-color: #ABE258;
      top: 0;
      left: 0;
    }
    &:nth-child(2){
      background-color: #6AA6FF;
      top: 0;
      right: 0;
      &:hover{

      }
    }
    &:nth-child(3){
      background-color: #FFB49C;
      bottom: 0;
      left: 0;
      &:hover{

      }
    }
    &:nth-child(4){
      background-color: #000;
      bottom: 0;
      right: 0;
      &:hover{

      }
    }
  }
  &__type-2{
    position: relative;
  }

}
@keyframes path-rotate {
  to{
    transform: rotate(18deg);

  }
}
.footer{
  &__wrapper-2{
    padding-bottom: 4.1rem;
    .footer__others{
      margin: 0;
      a{
        margin: 0;

        svg{
          @include respondTo(mobile) {
            width: 102px;
            height: 36px;
          }
          @include respondTo(small) {
            width: 68px;
            height: 24px;
          }

        }
      }
    }
    .footer__bottom {
      margin-top: 95px;
      padding-top: 16px;
      @include respondTo(mobile) {
        margin-top: 45px;
      }
      @include respondTo(small) {
        margin-top: 25px;
      }
    }
    .footer__top{
      @include respondTo(mobile) {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
      }

    }
    .footer__contacts{
      @include respondTo(mobile) {
        width: 100%;
      }
    }
    .footer__premium{
      @include respondTo(mobile) {
        margin-right: 35px;
        margin-top: 45px;
      }
      @include respondTo(small) {
        margin-right: 12px;
      }
    }
    .footer__others{
      @include respondTo(mobile) {
        margin-top: 45px;
      }
      @include respondTo(small) {
        margin-top: 30px;
      }
    }
  }
  &__premium{
    display: flex;
    align-items: center;
    padding-left: 12%;
    margin-top: -5px;
    margin-right: 10px;
    @include respondTo(tablet) {
      padding-left: 6%;
    }
    @include respondTo(mobile) {
      padding-left: 0;
      margin: 20px 0;
    }
  }
  &__premium-img{
    max-width: 63px;
    margin-right: 23px;
    @include respondTo(mobile) {
      max-width: 35px;
      margin-right: 15px;
    }
    @include respondTo(small) {
      max-width: 23px;
    }
  }
  &__premium-des{
    margin-top: -9px;
    p{
      font-size: 14px;
      line-height: 16px;
      color: #FFFFFF;
      &:first-child{
        font-family: $ff-medium;
      }
      &:last-child{
        font-family: $ff-base;
        margin-top: 5px;
      }

      @include respondTo(small) {
        font-size: 8px;
        line-height: 9px;
      }
    }
  }
}
