.btn {
    @extend %btn-reset;

    align-items: center;
    border: 1px solid $color-primary;
    border-radius: 40px;
    display: inline-flex;
    font-family: $ff-base;
    font-size: 1.25rem;//20px
    height: 2.75rem;//44px
    min-width: fit-content;
    line-height: 1.625rem;//26px
    padding: .5rem 1.25rem;//8px 20px
    transition: .3s background-color, .3s border-color, .3s color;
    justify-content: center;

    &:hover {
        background-color: $color-primary;
    }

    @include respondTo(tablet) {
        font-size: 1rem;//16px
        height: 2.5rem;//40px
        line-height: 1.25rem;//20px
    }

    @include respondTo(mobile) {
        height: 2.25rem;//36px
        letter-spacing: -0.015em;
        line-height: 1rem;//16px
    }

    &[disabled] {
        color: $color-text-gray-2;
        border-color: $color-text-gray-2;
        pointer-events: none;

        &:hover {
            background-color: transparent;
        }
    }

    &.disabled {
        color: $color-text-gray-2;
        border-color: $color-text-gray-2;
        pointer-events: none;

        &:hover {
            background-color: transparent;
        }
    }

    &_big {
        height: 4rem;//64px
        line-height: 1.4;
        padding: 1.125rem 1.25rem;//18px 20px

        @include respondTo(tablet) {
            height: 3rem;//48px
            line-height: 1.75;
            padding: .75rem 1.25rem;//12px 20px
        }

        @include respondTo(mobile) {
            height: 2.25rem;//36px
            line-height: 1.25;
            padding: .5rem 1.25rem;//8px 20px
        }
    }

    &_green {
        background-color: $color-primary;
        color: $color-text-white;

        & .upload-video {
            margin-left: 12px;
        }
    }

    &_green-n-black {
        background-color: $color-primary;
        color: $color-text;

        &:hover {
            color: $color-text-white;
        }
    }
  &_green-n-white {
    background-color: $color-primary;
    color: $color-text-white;

    &:hover {
      background-color: $color-text-white;
      color: $color-text;
    }
  }
  &_gray-bor-black {
    background-color: transparent;
    border: 1px solid #CACCDF;
    color: $color-text;

    &:hover {
      background-color: $color-text-white;
      color: $color-text;
    }
  }

    &_gray {
        background-color: $color-text-gray-3;
        border-color: $color-text-gray-3;
        color: $color-text-white;

        &:hover {
            background-color: $color-text-gray-3;
        }
    }

    &.full {
        width: 100%;
    }
}
